import React, {useEffect} from 'react';
import { useHistory } from "react-router-dom";
import care0 from "./img/care0.jpg";
import care1 from "./img/care1.png";
import care2 from "./img/care2.jpg";
import care3 from "./img/care3.jpg";
import Button from 'react-bootstrap/Button';
import foodDonate from './img/foodDonate.jpg';
import clothesDonate from './img/clothesDonate.jpg';
import moneyDonate from './img/moneyDonate.jpg';

const Home = () => {
    const history = useHistory();
    
    useEffect(() => {
        let savedEmail = sessionStorage.getItem("savedEmail");        
        if (savedEmail) {
            console.log("savedmail is " + savedEmail);
            styling();
        } else {
            history.push("/");
        }

        // https://codepen.io/desandro/pen/wjeBpp // carousel code
        var carousel = document.querySelector('.carousel');
        var cells = carousel.querySelectorAll('.carousel__cell');
        var cellCount; // cellCount set from cells-range input value
        var selectedIndex = 0;
        var cellWidth = carousel.offsetWidth;
        var cellHeight = carousel.offsetHeight;
        var isHorizontal = true;
        var rotateFn = isHorizontal ? 'rotateY' : 'rotateX';
        var radius, theta;
        
        // var prevButton = document.querySelector('.previous-button');
        // prevButton.addEventListener( 'click', function() {
        // selectedIndex--;
        // rotateCarousel(theta, selectedIndex, carousel, radius, rotateFn);
        // });

        // var nextButton = document.querySelector('.next-button');
        // nextButton.addEventListener( 'click', function() {
        // selectedIndex++;
        // rotateCarousel(theta, selectedIndex, carousel, radius, rotateFn);
        // });
        // isHorizontal = checkedRadio.value == 'horizontal';
        rotateFn = isHorizontal ? 'rotateY' : 'rotateX';
        // cellCount = cellsRange.value;
        cellCount = 5;
        theta = 360 / cellCount;
        var cellSize = isHorizontal ? cellWidth : cellHeight;
        radius = Math.round( ( cellSize / 2) / Math.tan( Math.PI / cellCount ) );
        for ( var i=0; i < cells.length; i++ ) {
            var cell = cells[i];
            if ( i < cellCount ) {
            // visible cell
            cell.style.opacity = 1;
            var cellAngle = theta * i;
            cell.style.transform = rotateFn + '(' + cellAngle + 'deg) translateZ(' + radius + 'px)';
            } else {
            // hidden cell
            cell.style.opacity = 0;
            cell.style.transform = 'none';
            }
        }
        rotateCarousel(theta, selectedIndex, carousel, radius, rotateFn);
    });

        
    function rotateCarousel(theta, selectedIndex, carousel, radius, rotateFn) {
        var angle = theta * selectedIndex * -1;
        carousel.style.transform = 'translateZ(' + -radius + 'px) ' + rotateFn + '(' + angle + 'deg)';
        selectedIndex++;
        console.log("turning");
        // rotateCarousel(theta, selectedIndex, carousel, radius, rotateFn);
        setTimeout(function(){ rotateCarousel(theta, selectedIndex, carousel, radius, rotateFn); }, 6000);
    }

    function styling() {
        let chapters = document.getElementsByClassName("chapter");
        for (let i = 0; i < chapters.length; i++) {
            if (i % 2 !== 0) {
                chapters[i].style.backgroundColor = "#DADADA";
            }
        }
        document.getElementsByClassName("Navbartop")[0].style.display = "block";
        document.getElementsByClassName("Footerbottom")[0].style.display = "block";
    }

    function donateMoney() {
        history.push("/donate-money");
    }
    
    function donateFood() {
        history.push("/donate-food");
    }

    function donateFoodPack1() {
        let myCart = sessionStorage.getItem("myCart");
        myCart += ",11";
        // myCart.push(btns[i].value);
        console.log(myCart);
        // sessionStorage.setItem("myCart", myCart.toString());
        sessionStorage.setItem("myCart", myCart);
        history.push("/my-cart");
    }

    function donateFoodPack2() {
        let myCart = sessionStorage.getItem("myCart");
        myCart += ",12";
        // myCart.push(btns[i].value);
        console.log(myCart);
        // sessionStorage.setItem("myCart", myCart.toString());
        sessionStorage.setItem("myCart", myCart);
        history.push("/my-cart");
    }

    function donateFoodPack3() {
        let myCart = sessionStorage.getItem("myCart");
        myCart += ",13";
        // myCart.push(btns[i].value);
        console.log(myCart);
        // sessionStorage.setItem("myCart", myCart.toString());
        sessionStorage.setItem("myCart", myCart);
        history.push("/my-cart");
    }

    function createPack() {
        history.push("/custom");
    }



    return (
        <div className="Home">
            <div className="HomeContent">
                <br />
                <div className="chapter">
                    <div className="scene">
                        <div className="carousel">
                            <div className="carousel__cell">
                                <img
                                    className="d-block"
                                    src={care1}
                                    alt="First slide"
                                />
                            </div>
                            <div className="carousel__cell">
                                <img
                                    className="d-block"
                                    src={care2}
                                    alt="First slide"
                                />
                            </div>
                            <div className="carousel__cell">
                                <img
                                    className="d-block"
                                    src={care3}
                                    alt="First slide"
                                />
                            </div>
                            <div className="carousel__cell">
                                <img
                                    className="d-block"
                                    src={care1}
                                    alt="First slide"
                                />
                            </div>
                            <div className="carousel__cell">
                                <img
                                    className="d-block"
                                    src={care2}
                                    alt="First slide"
                                />
                            </div>
                        </div>
                    </div>
                </div>
                <br />
                <div className="chapter">
                    <table className="homeTable">
                        <tbody>
                            <tr>
                                <td>
                                    <p>With your donations, food and supplies can be delivered to the poor families in Jabal Mohsen.</p>
                                    <Button variant="success" >Sponsor a family</Button>
                                </td>
                                <td>
                                    <img src={care0} alt="imagePic"></img>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>

                <br /><br />
                <div className="chapter">
                    <div id="donation">
                        <h3>Make a donation</h3>
                        <br />
                        <table className="donationTable">
                            <tbody>
                                <tr>
                                    <td>
                                        <div className="card purpleCard">
                                            <img className="card-img-top" src={"https://nypost.com/wp-content/uploads/sites/2/2020/12/everyplate2.jpg?quality=90&strip=all&w=1024"} alt="topleft" />
                                            <div className="card-body">
                                                <h5 className="card-title">Donate $50 Food Pack</h5>
                                                <p className="card-text"><li>Chicken</li><li>Potatoes</li><li>Rice</li></p>
                                                <button onClick={donateFoodPack1} className="btn btn-primary">Donate food</button>
                                            </div>
                                        </div>
                                    </td>
                                    <td>
                                        <div className="card greenCard">
                                            <img className="card-img-top" src={"https://png.pngitem.com/pimgs/s/375-3753134_broccoli-hd-png-download.png"} alt="topleft" />
                                            <div className="card-body">
                                                <h5 className="card-title">Donate $75 Food Pack</h5>
                                                <p className="card-text"><li>Beef</li><li>Tomatoes</li><li>Carrots</li></p>
                                                <button onClick={donateFoodPack2} className="btn btn-primary">Donate food</button>
                                            </div>
                                        </div>
                                    </td>                                    
                                    <td>
                                        <div className="card orangeCard">
                                            <img className="card-img-top" src={"https://batestownfarmproduce.ie/wp-content/uploads/2020/03/veg-box-300x300.jpg"} alt="topleft" />
                                            <div className="card-body">
                                                <h5 className="card-title">Donate $100 Food Pack</h5>
                                                <p className="card-text"><li>Tuna</li><li>Peas</li><li>Lentils</li></p>
                                                <button onClick={donateFoodPack3} className="btn btn-primary">Donate food</button>
                                            </div>
                                        </div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div className="card yellowCard">
                                            <img className="card-img-top" src={foodDonate} alt="topleft" />
                                            <div className="card-body">
                                                <h5 className="card-title">Create your own food pack<br /><br /></h5>
                                                <button onClick={createPack} className="btn btn-primary">Donate food</button>
                                            </div>
                                        </div>
                                    </td>
                                    <td>
                                        <div className="card redCard">
                                            <img className="card-img-top" src={clothesDonate} alt="topleft" />
                                            <div className="card-body">
                                                <h5 className="card-title">Donate Medicine and Medical Equipment</h5>
                                                <button onClick={donateFood} className="btn btn-primary">Donate</button>
                                            </div>
                                        </div>
                                    </td>
                                    <td>
                                        <div className="card blueCard">
                                            <img className="card-img-top" src={moneyDonate} alt="topleft" />
                                            <div className="card-body">
                                                <h5 className="card-title">Donate ZAKAAT<br /><br /></h5>
                                                <button onClick={donateMoney} className="btn btn-primary">Donate</button>
                                            </div>
                                        </div>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
            <br />
        </div>      
    );
}

export default Home;
